.container{
    width: 100%;
    height: 663px;
    display: flex;
    flex-direction: row;
}

h1, li {
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}

li{
    font-size: 16px;
    padding: 10px 5px;
    margin: 10px 5px;
}

.titulo{
    width: 100%;
    height: 12%;
    text-align: center;
    justify-content: center;
}

.filtro-portfolio{
    width: 100%;
    height: 12%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.class-projetos{
    display: grid;
}

.projeto{
    height: 200px;
    width: 250px;
    background-color: aqua;
}